body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.top-banner{
  background-image: url('./photos/codebanner.jpg');
  height: 100vh;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.top-banner h1{
  color: white;
  font-size: 60px;
}

.logo{
  width: 60%;
}


.intro-section{
  padding: 90px;
  display: flex;
  padding-top: 180px;
  padding-bottom: 180px;
}

.intro-section div{
  flex: 1;
}

.icon{
  background-color:white;
  margin:10px;
  font-size: 30px;
  padding: 15px;
  border-radius: 50px;
  margin-top: 30px;
}


h2, p {
  font-family: 'Poppins', sans-serif;
}

h2{
  font-size: 60px;
}

p{
  font-size: 24px;
}


.card {
  box-shadow: 0px 0px 10px #80808040;
  padding: 60px;
  border-radius: 15px;
  width: auto;
  background-color:white;
  min-width: 300px;
  text-align: center;
}

.card p {
  font-size: 36px;
}

.card i{
  font-size: 60px;
  text-align: center;

}


.card-carosel-parent{
  position: relative;
}

.card-icon{
  text-align: center;
}


.card h2{
  margin-top: 10px;
  margin-bottom: 0px;
  padding: 0px;
}

h2{
  padding: 0px;
  margin: 0px;
}

.card p{
  margin: 0px;
  padding: 0px;
}

.lesson-section{
  background-image: url('./photos/codingbanner.jpg');
  padding: 90px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
}


.card-section{
  background-color: white;
  border-radius: 15px;
  padding: 90px;
  width: 100%;
  box-shadow: 0px 0px 10px #80808040;
}

.card-section h2{
  text-align: center;
}

.play{
  font-size: 30px;
  
  cursor: pointer;

}

.flex-row{
  display: flex;
  justify-content: center;
  margin: 25px;
  text-align: center;
}

.flex-row div{
  margin: 10px;
  flex: 1;
}

iframe{
  height: 450px;
}


.app-demo div{
  padding: 30px;

}

.app-demo{
  align-items: center;
}

.wm{
  background-image: url('./photos/watermarkbackground.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}


@media(max-width: 1475px){
  .intro-section, .section{
    flex-direction: column !important;
    padding: 30px !important;
  }

  .icon {
    background-color: white;
    margin: 10px;
    font-size: 10px !important;
    padding: 7px !important;
    border-radius: 50px;
    margin-top: 30px;
}

.intro-section div{
  flex:auto;
}

.flex-row{
  flex-direction: column !important;
}

.card-section{
  padding: 0px !important;
}

.lesson-section{
  padding:30px;
  
}

.card-section{
  border-radius: 0px !important;
}

.flex-row{
  margin: 0px;
}

.app-demo div{
  padding: 0px;

}

.top-banner{
  height: 50vh;
}

.flex-row div{
  flex: auto !important;
}

 iframe{
  height: 250px !important;
}

.showcase-vid{
  width: 100% !important;
}

.card-carosel-parent{
  height: 400px;
}

}



